UI.registerHelper('MeteorToys', function(){
  return Session.get("MeteorToys_display");
});

Meteor.startup(function(){
  
  MeteorToys_ToyKit.hotKeys();
  MeteorToys_ToyKit.observe();
  Meteor.subscribe("MeteorToys");
});

MeteorToys_ToyKit = {
  observe: function () {
    
    // first load 
    MeteorToys_ToyKit.runPubSub();
    MeteorToys_ToyKit.runJetSetter();
    
    // start
    if (!Object.observe) {
      setInterval(function(){
        MeteorToys_ToyKit.runPubSub();
        MeteorToys_ToyKit.runJetSetter();
      }, 3000);
    } else {
      Object.observe(Meteor.default_connection._subscriptions, function() {
        MeteorToys_ToyKit.runPubSub();
      })
      Object.observe(Session.keys, function () {
        MeteorToys_ToyKit.runJetSetter();
      })
    }
    // end
  },
  hotKeys: function () {
    // start
    $(document).keydown(function (e) {
      if (e.keyCode === 77 && e.ctrlKey) {
        MeteorToys_ToyKit.toggleDisplay();
      }
    });
    // end
  },

  toggleDisplay: function () {

    var displayStatus = Session.get("MeteorToys_display");

    if (displayStatus) {
      Session.set("MeteorToys_display", false);
    } else {
      Session.set("MeteorToys_display", true);
    }

  },
  runJetSetter: function () {
    if (Package["msavin:jetsetter"]) {
      Package["msavin:jetsetter"].JetSetter.getKeys()
    }
  },
  runPubSub: function () {
    if (Package["msavin:pubsub"]) {
      Package["msavin:pubsub"].MeteorToys_PubSub.getSubscriptionKeys();
    } else if (Package["msavin:mongol"]) {
      Package["msavin:mongol"].MongolPackage.setSubscriptionKeys()
    }
  }
}

